@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
*,  *::after, *::before {
	box-sizing: border-box;
}

.anim-me *, .anim-me *::after, .anim-me *::before {
	transition: all 500ms;
}

:root {
	--background-color: #121212;
	--body-color: #1e1e1e;
	--body-interior-color: #2b2b2b;
	--font-color: #eee;

	--confirm-color: #3cb180;
	--deny-color: #df5858;
}

body, html, #root, #root>div {
	font-family: Raleway;
	height: 100%;
	margin: 0;
}

body {
	background-color: #121212;
	background-color: var(--background-color);
	color: #eee;
	color: var(--font-color);
}
.App {
	min-height: 100%;
}

.container {
	background: var(--body-color);
	border-radius: .5em;
	max-width: 768px;
	padding: 1em 1em;
	margin: 1em;
}

button {
	font-family: Raleway, sans-serif;
	background: none;
	font-size: 1rem;
	cursor: pointer;
}

.containerHeader {
	opacity: 9;
	font-weight: 400;
	text-align: center;
	padding-bottom: .5em;
	position: relative;
	margin: 0 auto;
	background-color: var(--body-color);
}

p, h1, h2, h3, h4, h5, h6 {
	padding: 0;
	margin: 0;
}

.stylized-btn {
	border: 2px solid var(--body-color);
	border-radius: 1em;
	padding: .5em 3em;
	margin: 2em auto;
	background: none;

	color: var(--font-color);
	text-decoration: none;
}

.stylized-btn:hover, .stylized-btn:focus {
	background-color: var(--body-color);
}

@media (min-width: 768px) {
	.container {
		margin: 1em auto;
	}
}
/*Imagegrid
====================*/

@media (min-width: 768px) {
	.images {
		grid-template-columns: repeat(auto-fill, minmax(10em, 1fr));
	}
}

.images {
	display: grid;
	grid-gap: 1em;
	grid-template-columns: repeat(auto-fill, minmax(9em, 1fr));
}

/*Image entry
====================*/

.image-entry {
	box-shadow: 0px 0px 10px var(--body-interior-color);
	background-color: var(--body-interior-color);
	border: .2rem solid var(--body-interior-color);
	border-radius: .5em;
	overflow: hidden;
	font-family: Roboto, sans-serif;
}

.image-entry h3 {
	text-align: center;
	font-size: 1rem;
	padding: .2em;
	opacity: .75;
	font-weight: 300;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.extended-info {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	font-weight: 300;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	padding: .2em .5em .4em .5em;
	opacity: .5;
}

.img {
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	width: 100%;
}

.img:after {
	content: "";
	display: block;
	padding-bottom: 100%;
}

.entry-popin {
	-webkit-animation: entry-popin .5s ease-in-out 0s;
	        animation: entry-popin .5s ease-in-out 0s;
}

@-webkit-keyframes entry-popin {
	0% {
		-webkit-transform: scale(.5);
		        transform: scale(.5);
		opacity: 0;
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		opacity: 1;
	}
}

@keyframes entry-popin {
	0% {
		-webkit-transform: scale(.5);
		        transform: scale(.5);
		opacity: 0;
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		opacity: 1;
	}
}

/*Loading
====================*/

.imgGrid-Loading {
	text-align: center;
	font-weight: 300;
	padding: 3rem 0 2rem 0;
}

.imgGrid-Loading>span {
	-webkit-animation-name: periodBounce;
	        animation-name: periodBounce;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
	display: inline-block;
	padding-left: .2rem;
	font-weight: 500;
}

.imgGrid-Loading>span:nth-child(2) {
	-webkit-animation-delay: .25s;
	        animation-delay: .25s;
}

.imgGrid-Loading>span:nth-child(3) {
	-webkit-animation-delay: .5s;
	        animation-delay: .5s;
}

@-webkit-keyframes periodBounce {
	0%, 50% {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
	25% {
		-webkit-transform: translateY(-.25rem);
		        transform: translateY(-.25rem);
	}
}

@keyframes periodBounce {
	0%, 50% {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
	25% {
		-webkit-transform: translateY(-.25rem);
		        transform: translateY(-.25rem);
	}
}

/*Returnto
====================*/

.imgGrid-return {
	padding: 1.5rem 0 .5rem 0;

	display: -webkit-flex;

	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	opacity: .8;
}

.imgGrid-return h3 {
	font-style: italic;
	text-align: center;
	font-weight: 300;
}

.imgGrid-return button {
	border: 1px solid var(--font-color);
	border-radius: 1em;
	margin-top: .5em;

	color: var(--font-color);
	text-decoration: none;
	padding: .25em .5em;
	opacity: .8;
}

.imgGrid-return button:hover, .imgGrid-return button:focus {
	background-color: var(--font-color);
	color: var(--body-color);
	opacity: 1;
}
/*Image
====================*/


.modalImage {
	font-family: Roboto, sans-serif;
	background-color: rgba(0, 0, 0, .6);
	display: grid;
	place-items: center;
	position: fixed;
	height: 100vh;
	width: 100vw;
	left: 0;
	top: 0;

	z-index: 1000;
}

.modalImage-inner {
	background-color: var(--body-interior-color);
	border-radius: .5em;
	padding: .2em 1em;
}



/*Topbar
====================*/

.modalImage-topbar {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	font-size: .8rem;
}

.modalImage-topbar a {
	color: var(--font-color);
	text-decoration: none;
	font-weight: 400;
	font-size: 1rem;
