}

/*Image
====================*/

.modalImage-imgholder {
	display:grid;
	place-items: center;
	min-width: 50vw;

	background-color: var(--body-color);
	border-radius: .5rem;
	overflow: hidden;
	margin: 0 0 0 0;
}

.modalImage-imgholder img {
	max-height: 75vh;
	max-width: 75vw;
	height: auto;
	width: auto;
}

/*Bottombar
====================*/

.modalImage-titlebar {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
}

.modalImage-titlebar button {
	color: var(--font-color);
	font-size: 1rem;
	opacity: .4;
	background: none;
	border: none;
}

.modalImage-titlebar button:hover, .modalImage-titlebar button:focus {
	color: var(--deny-color);
	cursor: pointer;
	opacity: 1;
}

.modalImage-bottombar {
	-webkit-align-items: center;
	        align-items: center;
	padding: .5rem 0;
}

.modalImage-bottombar h2 {
	font-size: 1.25rem;
	font-weight: 400;
}

.modalImage-bottombar p {
	font-size: 1rem;
	font-weight: 300;
	opacity: .8;
}

@media (min-width: 768px){
	.modalImage-bottombar {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: row;
		        flex-direction: row;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
	}
}
#notfound {
	background: conic-gradient(from 90deg at 50% 0%, #111, 50%, #222, #111);
	font-family: roboto, sans-serif;
	text-align: center;
	display: grid;
	place-items: center;
	height: 100vh;
	width: 100vw;
}

#notfound h2 {
	background: conic-gradient(at 125% 50%, #b78cf7, #ff7c94, #ffcf0d, #ff7c94, #b78cf7);
	-webkit-background-clip: text;
	background-clip: text;
	color: transparent;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;

	font-size: 10rem;
	font-weight: 100;
}

#notfound span + span {
	padding-left: 3rem;
}

#notfound-inner a {
	margin-top: 2rem;
	display: block;
	width: 10em;
}


#notfound #flicker {
	-webkit-transform: rotate(-15deg) translate(-.4rem, .3rem);
	        transform: rotate(-15deg) translate(-.4rem, .3rem);
	display: inline-block;

	-webkit-animation-name: flicker;

	        animation-name: flicker;
	-webkit-animation-timing-function: steps(1, end);
	        animation-timing-function: steps(1, end);
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-duration: 5s;
	        animation-duration: 5s;
	transition: none;
}

@-webkit-keyframes flicker {
	0%, 21%, 23%, 25%, 50%, 75%, 77% {
		background: none;
		color: white;
	}
	1%, 22%, 24%, 26%, 54%, 76%, 78% {
		background: inherit;
		color: transparent;
	}
}

@keyframes flicker {
	0%, 21%, 23%, 25%, 50%, 75%, 77% {
		background: none;
		color: white;
	}
	1%, 22%, 24%, 26%, 54%, 76%, 78% {
		background: inherit;
		color: transparent;
	}
}
